import React from 'react';
import Button from '../Button/Button';
import ImageFileInput from '../Image_file_input/ImageFileInput';
import styles from './CardEdit.module.css';
const CardEdit = ({ card }) => {
  const { id, name, company, theme, professional, email, message } = card;

  const onSubmit = (event) => {
    event.preventDefault();
  };
  return (
    <form className={styles.form} key={id}>
      <input className={styles.input} type="text" name="name" value={name} />
      <input className={styles.input} type="text" name="company" value={company} />
      <select className={styles.select} name="theme" value={theme}>
        <option value="light">Light</option>
        <option value="dark">Dark</option>
        <option value="colorful">Colorful</option>
      </select>
      <input className={styles.input} type="text" name="professional" value={professional} />
      <input className={styles.input} type="text" name="email" value={email} />
      <textarea className={styles.textarea} name="message" value={message}></textarea>
      <div className={styles.fileInput} onClick={onSubmit}>
        <ImageFileInput />
      </div>
      <Button name="Delete" onClick={onSubmit} />
    </form>
  );
};

export default CardEdit;
